import React, { useState } from 'react';
import { Box, Typography, Button, useMediaQuery, Theme } from '@mui/material';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles({
  root: {
    position: 'relative',
    maxWidth: '1100px', 
    '&::before': {
      content: '""',
      position: 'absolute',
      top: 0,
      left: 0,
      backgroundImage: 'url(./assets/images/bg_about.png)', 
      backgroundSize: 'cover',
      width: '100%',
      height: '100%',
      borderRadius: '50px',
    }
  }
})

const HeroSection: React.FC = () => {
  const classes = useStyles();
  const isSmallScreen = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));
  const [showMore, setShowMore] = useState(false);

  return (
    <Box
      sx={{
        px: isSmallScreen ? 2 : 12,
        py: isSmallScreen ? 3 : 6,
        my: isSmallScreen ? 2 : 3,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}
      className={classes.root}
    >
      <Typography 
        variant="h5" 
        component="h2" 
        sx={{ 
          fontWeight: 'bold', 
          mb: 2, 
          zIndex: 1, 
          position: 'relative', 
          fontSize: isSmallScreen ? 24 : 36, 
          textAlign: 'center' 
        }}
      >
        Allowing anyone to rent cpu or deposit wax and earn from renting to others.
      </Typography>
      
      {(!isSmallScreen || showMore) && (
        <Box>
          <Typography variant="body1" sx={{ zIndex: 1, position: 'relative', textAlign: 'center', fontSize: isSmallScreen ? 16 : 24}}>
            The price is dynamically set between a 1% and 10% fee and it changes every time someone deposits/withdraws/rents/returns wax.
          </Typography>
          <Typography variant="body1" sx={{ zIndex: 1, position: 'relative', textAlign: 'center', fontSize: isSmallScreen ? 16 : 24}}>
            Users are limited to how much they can rent in a single transaction but can make multiple requests as long as there is wax available. There is also a free system implemented but it has limits too.
          </Typography>
          <Typography variant="body1" sx={{ zIndex: 1, position: 'relative', textAlign: 'center', fontSize: isSmallScreen ? 16 : 24}}>
            Users are limited to how much they can rent in a single transaction but can make multiple requests as long as there is wax available. There is also a free system implemented but it has limits too.
          </Typography>
        </Box>
      )}

      {isSmallScreen && !showMore && (
        <Button
          onClick={() => setShowMore(true)}
          sx={{
            mt: 2,
            color: 'white',
            bgcolor: '#882140',
            borderRadius: '4px',
            padding: '8px 24px',
            fontSize: '14px',
            '&:hover': {
              bgcolor: '#6d1a33'
            }
          }}
        >
          Read More
        </Button>
      )}

      {isSmallScreen && showMore && (
        <Button
          onClick={() => setShowMore(false)}
          sx={{
            mt: 2,
            color: 'white',
            bgcolor: '#882140',
            borderRadius: '4px',
            padding: '8px 24px',
            fontSize: '14px',
            '&:hover': {
              bgcolor: '#6d1a33'
            }
          }}
        >
          Show Less
        </Button>
      )}
    </Box>
  );
};

export default HeroSection;